@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  background: #fbfbfb;
}

.Content {
  min-height: 80vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: grey;
}

h1 {
  font-size: 24px;
  line-height: 28px;
}

h2 {
  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  font-size: 36px;
  line-height: 42px;
  text-align: center;

  color: #000000;
}

.Auth-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.Auth-item {
  margin: 2em 0 2em 0;
}

label {
  font-style: normal;
  font-weight: bold;
  font-size: 36px;
  line-height: 42px;
  text-align: center;
  color: #000000;
}

input {
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
  outline: 0;
  width: 330px;
  border-width: 0 0 2px;
  border-color: lightgray;
}

#submit {
  border: none;
  width: 94px;
  height: 36px;
  background: #009688;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.24), 0px 0px 2px rgba(0, 0, 0, 0.12);
  border-radius: 2px;
  color: white;

  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.0357143em;
}

/* TOPBAR */
.TopBar-container {
  display: flex;
  flex-direction: row;
  align-items: baseline;
  justify-content: space-around;
  background-color: white;
  height: 64px;
}

.TopBar-item {
  width: auto;
  margin: auto; /* Magic! */
}

#buttonLogout {
  border: none;
  background: #000000;
  color: white;
  border-radius: 5px;
  height: 40px;
  -webkit-transition: background 0.1s;
  transition: background 0.1s;
}

#buttonLogout:hover {
  background: #444444;
  -webkit-transition: background 0.2s;
  transition: background 0.2s;
}

h2.TopBar-item {
  font-size: 18px;
  line-height: 21px;
}

.rowTd {
  color: black;
  background: #f9f9f9;
  height: 48px;
  text-align: left;
  padding-left: 8px;
  font-size: 14px;
  border-bottom: 1px solid #c4c4c4;
}

#doneAction {
  border: none;
  background: none;
  width: 80px;
  color: yellow;
  text-align: left;
}

#editAction {
  border: none;
  background: none;
  width: 80px;
  color: green;
  text-align: left;
}

#deleteAction {
  border: none;
  background: none;
  width: 40px;
  color: red;
  text-align: left;
}

.overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.7);
}

.popup {
  margin: 70px auto;
  padding: 20px;
  background: #fff;
  border-radius: 5px;
  width: 30%;
  position: relative;
}

.content {
  display: flex;
  height: 400px;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
}

.child {
  width: 200px;
  margin: auto;
}

#labelAdd {
  width: 300px;
}

#close {
  position: absolute;
  top: 20px;
  right: 30px;
  -webkit-transition: all 200ms;
  transition: all 200ms;
  font-size: 30px;
  font-weight: bold;
  text-decoration: none;
  border: none;
  background: none;
  color: #333;
}

/* TABLE */
.Actions-List {
  border-collapse: collapse;
}
.Actions-tr td {
  color: black;
  background-color: white;
  border-bottom: 1px solid #c4c4c4;
}

table {
  margin: 2em;
  max-width: 1100px;
}

#addAction {
  border: none;
  width: 120px;
  height: 36px;
  background: #009688;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.24), 0px 0px 2px rgba(0, 0, 0, 0.12);
  border-radius: 2px;
  color: white;

  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.0357143em;
}

